.block {
  position: relative;
  flex-grow: 0;
  width: 272px;
  height: 208px;
  cursor: grab;
}

.price {
  height: 24px;
  align-self: center;
  display: flex;
  flex-direction: row;
}

.title {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 40px;
  text-align: center;
  width: 640px;
  height: 64px;
}

.image {
  width: 520px;
  height: 240px;
  align-self: center;
}

.frame {
  align-self: center;
  text-align: center;
  margin-top: 16px;
  width: 520px;
  min-height: 30px;
}
.nutrition {
  gap: 20px;
  margin-top: 32px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.value {
  width: 120px;
  height: 56px;
  text-align: center;
}
